ton type=\"text\"\n                       @click=\"DetailsVisible=true,details(scope.row)\">{{scope.row.name}}</el-button>\n          </template>\n        </el-table-column>\n        <el-table-column label=\"城市\"\n                         align=\"center\"\n                         prop=\"city\"></el-table-column>\n        <el-table-column label=\"产品名称\"\n                         align=\"center\"\n                         prop=\"goods_name\"></el-table-column>\n        <el-table-column label=\"产品品牌\"\n                         align=\"center\"\n                         prop=\"goods_brand\"></el-table-column>\n      </el-table>\n      <!-- 供应商信息详情 -->\n      <el-dialog title='供应商信息详情'\n                 center\n                 :visible.sync=\"DetailsVisible\"\n                 width=\"30%\">\n        <div v-if='detailsList'>\n          <div class=\"head_pic\">\n            <!-- <p>头像</p> -->\n            <img :src=\"detailsList.head_pic\">\n          </div>\n          <div class=\"details\">\n            <p>\n              <span>供应商名称: </span>\n              <span>{{detailsList.name}}</span>\n            </p>\n            <p>\n              <span>电话: </span>\n              <span>{{detailsList.phone}}</span>\n            </p>\n\n          </div>\n        </div>\n      </el-dialog>\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      list: [],\n      seCurId: '',\n      token: window.sessionStorage.getItem('bbytoken'), //token令牌\n      authList: [],\n      page: 0,  //供应商列表当前页数\n      pageCount: 0, //供应商列表总页数\n      DetailsVisible: false,\n      sm_name: '',\n      detailsList: {}\n    }\n  },\n\n  methods: {\n    paging (e) {  //获取供应商列表当前页\n      this.page = e\n      this.init()\n    },\n    search () {  //搜索\n      this.init()\n    },\n    async init () {\n      try {\n        const res = await this.$axios.post('admin/SmList/getFreeIndexList', { token: this.token, page: this.page, sm_name: this.sm_name })\n        this.list = res.data.data.list || []\n        this.pageCount = res.data.data.rows || 0\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //供应商信息详情\n    async details (row) {\n      try {\n        const res = await this.$axios.post('admin/SmList/getSmUserInfo', { token: this.token, sm_id: row.id })\n        this.detailsList = res.data.data\n      } catch (err) {\n        throw (err)\n      }\n    },\n\n\n    // 权限列表,当前所在权限页  \n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data || [];\n          this.authList = arr;\n          console.log(this.authList)\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  created () {\n    this.erAuth()\n  },\n  mounted () {\n\n    this.init() //初始化数据  \n\n  },\n}\n</script>\n<style scoped>\n.search {\n  width: 100%;\n  overflow: hidden;\n}\n.search {\n  padding: 0px 0px 20px 50px;\n}\n.search >>> .el-input {\n  width: 20%;\n}\n.search >>> button {\n  padding: 11px 15px;\n}\n\n.head_pic {\n  text-align: center;\n}\n.head_pic > p {\n  padding: 5px 0;\n}\n.head_pic > img {\n  width: 60px;\n  height: 60px;\n  border-radius: 50%;\n}\n.details span {\n  display: inline-block;\n  width: 48%;\n  padding: 10px 0;\n}\n.details p > span:last-child {\n  text-align: right;\n}\n</style>"],"sourceRoot":""}]);

